import React, { Component } from 'react';
import { connect } from 'react-redux';
import Input from 'element-react/dist/npm/es5/src/input';
import Button from 'element-react/dist/npm/es5/src/button';
import { bindActionCreators } from 'redux';
import i18n from '../../language';
import images from "../../images";
import Message from '../Parts/Message/Message';
import './MsgBox.scss';

class From extends Component {
  state = {
    focusOn: 'PRDT',
    PRDTValue: '',
    EOSValue: '',
    inputError: false
  };

  EOSFocus = () => {
    this.setState({
      focusOn: 'EOS'
    })
  };

  PRDTFocus = () => {
    this.setState({
      focusOn: 'PRDT'
    })
  };

  onClick = () => {
    const { focusOn, PRDTValue, EOSValue } = this.state;
    const { exchangeRate } = this.props;

    // const value = focusOn === 'PRDT' ? PRDTValue : EOSValue;

    const passed = /^\d+.?\d{0,4}$/.test(EOSValue);

    if (!passed) {
      Message('error_input_invalid', { type: 'error' });
      return;
    }

    if (EOSValue < this.props.minPayments) {
      Message('error_input_too_small', { type: 'error' });
      return;
    }

    const PRDTQuantity = focusOn === 'PRDT' ? PRDTValue : EOSValue * exchangeRate;
    const EOSQuantity = focusOn === 'EOS' ? EOSValue : PRDTValue / exchangeRate;
    console.log(focusOn, PRDTQuantity, EOSQuantity);
    this.props.confirm(focusOn, this.state.PRDTValue,  this.state.EOSValue);
  };

  render() {
    const { focusOn } = this.state;
    const { exchangeRate } = this.props;

    const PRDTClassName = focusOn === 'PRDT' ? 'PRDT-input PRDT-input-active' : 'PRDT-input';
    const EOSClassName = focusOn === 'EOS' ? 'EOS-input EOS-input-active' : 'EOS-input';
    return (
      <div className="modal-from-wrapper">
        <a href="#" onClick={this.props.close}>
          <img className="el-icon-close modal-btn-close" src={images.menuClose} alt="" />
        </a>
        <p className="modal-form-title">{i18n.t('buy_PRDT_title')}</p>
        <p className="modal-form-content">{i18n.t('buy_PRDT_subtitle')}</p>
        <Input
          type='number'
          value={this.state.PRDTValue}
          className={PRDTClassName}
          placeholder={i18n.t('placeholder_PRDT_qty')}
          onChange={(value) => {
            if (value.length >= 12) return;
            this.setState({
              PRDTValue: value,
              EOSValue: parseFloat((value / exchangeRate).toFixed(4), 10) + ''
            });
          }}
          onFocus={this.PRDTFocus}
        />
        <img className="modal-from-img-exchange" src={images.exchange} alt=""/>
        <Input
          type='number'
          value={this.state.EOSValue}
          className={EOSClassName}
          placeholder={i18n.t('placeholder_EOS_qty')}
          onChange={(value) => {
            if (value.length >= 12) return;
            this.setState({
              EOSValue: value,
              PRDTValue: parseFloat((value * exchangeRate).toFixed(4), 10) + ''
            });
          }}
          onFocus={this.EOSFocus}
        />
        <Button onClick={this.onClick} className="modal-btn-confirm" type="primary">{i18n.t('btn_next')}</Button>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    progress: state.icoInfo.progress * 100,
    exchangeRate: state.icoInfo.exchangeRate
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(From);
