// 合约操作0xa4992afa677abe87d20f559d61e8558d478f6b20
export function transfer({
  contract = 'eosio.token',
  actionName = 'transfer',
  to,
  quantity = '',
  memo = ''
}) {
  return (dispatch, getState) => {
    const { identity = {} } = getState();
    if (!identity) {
      return Promise.reject('未登陆账户');
    }
    // // contract方式交易
    // return EOSClient.contract('ethsidechain')
    //   .then(r => {
    //     r.transfer(
    //       identity.name,
    //       to,
    //       '1.0000 EETH',
    //       memo,
    //       { authorization: [`${identity.name}@${identity.authority}`]}
    //     ).then(r => {
    //       console.log(r, 'transfer');
    //     })
    //   });

    // transaction方式
    // console.log(getEOSClient(), 'getEOSClient()')
    return getEOSClient().transaction(
      {
        // ...headers,
        actions: [
          {
            account: contract,
            name: actionName,
            authorization: [{
              actor: identity.name,
              permission: identity.authority
            }],
            data: {
              from: identity.name,
              to: to,
              quantity,
              memo
            }
          }
        ]
      }
    )
      .catch(err => {
        return Promise.reject(err);
      });
  }
}

