import React, {Component} from 'react';
import Layout from 'element-react/dist/npm/es5/src/layout';
import Button from 'element-react/dist/npm/es5/src/button';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import QRCode from 'qrcode.react';
import {transfer} from '../../actions';
import Message from '../Parts/Message/Message';
import i18n from '../../language';
import { formatNumber } from '../../utils';
import './MsgBox.scss';
import images from "../../images";


class Buy extends Component {
  state = {
    fetching: false
  };

  buy = () => {
    if (!this.props.identity) {
      // 未登陆
      Message('not_login', { type: 'error' });
      return;
    }

    this.setState({fetching: true});

    const { EOSQuantity, account } = this.props.data || {};

    // if (EOSQuantity < this.props.minPayments) {
    //   Message('error_input_too_small', { type: 'error' });
    //   this.setState({fetching: false});
    //   return;
    // }

    // transfer
    this.props.transfer({
      actionName: 'transfer',
      to: account,
      quantity: parseFloat(EOSQuantity).toFixed(4) + ' EOS',
    })
      .then(r => {
        this.setState({fetching: false});
        this.props.confirm && this.props.confirm();
      })
      .catch(err => {
        this.props.confirm && this.props.close();
        Message('pay_fail', { type: 'error' });
      });
  };

  render () {
    const { exchangeRate, data = {} } = this.props || {};
    const { EOSQuantity, account } = data;
    const { fetching } = this.state;
    // console.log(PRDTQuantity, EOSQuantity, 999999)

    // 这里的TTC数量应由实际支付EOS转换，可能对原输入TTC数量修整精度
    const PRDTQuantityFinal = formatNumber(parseFloat((EOSQuantity * exchangeRate).toFixed(4)));
    return (
      <div className="modal-buy-wrapper">
        <a href="#" onClick={this.props.close}>
          <img className="el-icon-close modal-btn-close" src={images.menuClose} alt="" />
        </a>
        <p className="title-medium-light align-center">{i18n.t('payment_title')}</p>
        <div className="modal-payment-wrapper">
          <Layout.Row>
            <Layout.Col span={12} className="text-small-light align-left">{this.props.name}</Layout.Col>
            <Layout.Col span={12} className="text-small-light align-right omit">{PRDTQuantityFinal}</Layout.Col>
          </Layout.Row>
          <Layout.Row className="modal-payment-row">
            <Layout.Col span={12} className="text-small-light align-left">EOS</Layout.Col>
            <Layout.Col span={12} className="text-small-light align-right omit">{formatNumber(EOSQuantity)}</Layout.Col>
          </Layout.Row>
          <Layout.Row className="modal-payment-row">
            <Layout.Col span={12} className="text-small-light align-left">{i18n.t('PRDT_account')}</Layout.Col>
            <Layout.Col span={12} className="text-small-light align-right">{account}</Layout.Col>
          </Layout.Row>
        </div>
        <p className="payment-text-scan">{i18n.t('payment_scan')}</p>
        <div className="qr-code-wrapper">
          <QRCode
            value={`eos:${account}?value=${EOSQuantity}`}
            size={128}
          />
        </div>

        <p className="text-tiny-light-s align-center">{i18n.t('payment_tips')}</p>
        <Button disabled={fetching} icon={fetching ? 'loading' : ''} onClick={this.buy} className="modal-btn-buy-now" type="primary">{i18n.t('btn_buy')}</Button>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    progress: state.icoInfo.progress * 100,
    exchangeRate: state.icoInfo.exchangeRate,
    minPayments: state.icoInfo.minPayments,
    name: state.icoInfo.name,
    identity: state.identity
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({transfer}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Buy);

