import React, {Component} from 'react';
import Button from 'element-react/dist/npm/es5/src/button';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {transfer} from '../../actions';
import i18n from '../../language';
import './MsgBox.scss';
import images from "../../images";


class Buy extends Component {
  render () {
    return (
      <div className="modal-success-wrapper">
        <a href="#" onClick={this.props.close}>
          <img className="el-icon-close modal-btn-close" src={images.menuClose} alt="" />
        </a>
        <p className="payment-success-title">{i18n.t('payment_success_title')}</p>
        <p className="payment-success-tips">{i18n.t('payment_success_tips')}</p>
        <p className="payment-success-tips-1">{i18n.t('payment_success_tips_1')}</p>
        <p className="payment-success-tips-2">{i18n.t('payment_success_tips_2')}</p>
        <p className="payment-success-tips-3">{i18n.t('payment_success_tips_3')}</p>

        <Button onClick={this.props.confirm} className="modal-btn-buy-now" type="primary">{i18n.t('btn_ok')}</Button>
        <div className="payment-success-line" >
          <span className="payment-success-text-service">{i18n.t('PRDT_service')}</span>
        </div>
        <a href="#" onClick={() => { window.location.href = this.props.customerService }}>
          <img className="payment-btn-service" src={images.telegram} alt="telegram"/>
        </a>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    customerService: state.customerService
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({transfer}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Buy);

