import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import images from '../../../images';
import './SocialContact.scss';

class Link extends Component {
  render() {
    // 分享
    // href="https://telegram.me/share/url?url=http://10.10.0.52:3001"
    // href="tg://msg?text=Mi_mensaje&to=+1555999

    return (
      <div className="social-wrapper">
        <a href={this.props.socialContact} className="text-small-light">
          <img className="social-img-telegram" src={images.telegram} alt="telegram"/>
        </a>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    socialContact: state.socialContact
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Link);
