import {UPDATE_PRDT_INFO} from "./index";
import Alertor from "../Utils/Alertor";
import { toggleNetwork, getReadOnlyEOS } from "../networks/index";

export const UPDATE_REMOTE_TIME = Symbol('UPDATE_REMOTE_TIME');
export const UPDATE_TIME_LEFT = Symbol('UPDATE_TIME_LEFT');

// 校对时间
export function getRemoteTime() {
  return (dispatch, getState) => {
    // 模拟获取服务器时间
    Promise.resolve(Date.now() - parseInt(Math.random()* 5000 - 2500))
      .then((r) => {
        const timeOffset = r - Date.now();
        dispatch({ type: UPDATE_REMOTE_TIME, payload: { timeOffset } });
        console.log(timeOffset, '11111-------')
      })
  }
}

// 计算剩余时间 (获取到round信息后)
export function startCount() {
  return (dispatch, getState) => {
    timeInspector(1000, () => {
      const { icoInfo = {}, timeOffset } = getState();
      const timeLeft = icoInfo.endTime - Date.now() + timeOffset;
      dispatch({ type: UPDATE_TIME_LEFT, payload: { timeLeft } });
    })()
  }
}

// 轮询 tableRow
export function getTableRows() {
  const alertor = new Alertor({
    name: 'fetchAlertor',
    threshold: 3,
    alarm: () => toggleNetwork()
  });

  return (dispatch, getState) => {
    const { contract } = getState().icoInfo;
    timeInspector(3000, () => {
      getReadOnlyEOS()
        .getTableRows({
          code: contract,
          json: true,
          scope: 'PRDT',
          table: 'stat',
        })
          .then((res) => {
            console.log(res, 'tablerows');
            const { occupy } = getState().icoInfo;
            const { rows = [] } = res || {};
            const { Largest_inventory_: remain, max_supply: maxSupply, exchange_rate_: exchangeRate, start_time, end_time, threshold_: minPayments, end_ } = rows[0];
            const progress = 1 - ((remain / 10000) / (parseFloat(maxSupply) * occupy));
            dispatch({
              type: UPDATE_PRDT_INFO,
              payload: {
                progress,
                maxSupply: parseFloat(maxSupply),
                exchangeRate,
                startTime: start_time * 1000,
                endTime: end_time * 1000,
                minPayments,
                end: end_ === 1
              }
            });
          })
          .catch(err => {
            alertor.add();
            console.warn(err);
          });
    })();
  }
}


// 间隔检查器
function timeInspector(time, callback) {
  let lastTime = 0;
  return function iterate() {
    if (Date.now() - lastTime >= time) {
      lastTime = Date.now();
      callback()
    }
    requestAnimationFrame(() => iterate());
  }
}
