import i18next from 'i18next';
import en from './locales/en.json';
import ch from './locales/ch.json';
import ch1 from './locales/ch1.json';

i18next.init({
  lng: 'ch',
  debug: false,
  resources: {
    en: {
      translation: en
    },
    ch: {
      translation: ch
    },
    ch1: {
      translation: ch1
    }
  },
  fallbackLng: ["ch", "ch1", "en"]
}, function (err, t) {
  // init set content
  if (err) {
    console.log('i18n init', t, err);
  }
});

export default i18next;

