/*
 * 第一屏：购买
 **/
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Card from 'element-react/dist/npm/es5/src/card';
import Progress from 'element-react/dist/npm/es5/src/progress';
import Button from 'element-react/dist/npm/es5/src/button';
import i18n from '../../language';
import ContactUs from '../Parts/ContactUs/SocialContact';
import TimeLeft from '../Parts/TimeLeft/TimeLeft';
import Modal from '../Modal/Modal';
import './main.scss';


class Main extends Component {
  state = {
    modalOpen: false,
    modalType: 'modal-form',
    coinType: void (0),
    PRDTQuantity: 0,
    EOSQuantity: 0
  };

  isEnded;

  onClick = () => {
    if (this.isEnded) {
      // 加入电报群
      window.location.href = this.props.socialContact;
    } else {
      this.setState({ modalOpen: true });
    }
  };

  onModalClose = () => {
    this.setState({ modalOpen: false });

    // 动画结束后
    setTimeout(() => {
      this.setState({ modalType: 'modal-form' })
    }, 300)
  };

  onModalConfirm = (coinType, PRDTQuantity, EOSQuantity) => {
    if (this.state.modalType === 'modal-form') {
      this.setState({
        modalType: 'modal-buy',
        coinType,
        PRDTQuantity,
        EOSQuantity
      })
    } else if (this.state.modalType === 'modal-buy') {
      this.setState({ modalType: 'modal-success' });
    } else {
      this.setState({ modalOpen: false });

      // 动画结束后
      setTimeout(() => {
        this.setState({ modalType: 'modal-form' })
      }, 300)
    }
  };

  render() {
    const { coinType, PRDTQuantity, EOSQuantity } = this.state;
    const { account, timeLeft } = this.props;

    const progress = `${parseFloat(parseFloat(this.props.progress).toFixed(2))}%`;
    const { flexible = {} } = window.lib || {};
    let strokeWidth = 8;
    if (flexible.rem) {
      strokeWidth = 0.19 * flexible.rem;
    }

    this.isEnded = parseInt(this.props.progress) === 100 || timeLeft <= 0 || this.props.end;

    return (
      <div className="main-wrapper">
        <div className="row-center">
          <div>
            <p className="main-slogan">{i18n.t('slogan')}</p>
            <p className="main-subhead">{i18n.t('subhead')}</p>
            <span className="text-follow">{i18n.t('follow_us')}</span>
            <ContactUs />
          </div>
        </div>

        <Card className="sell-card">
          <p className="sell-card-title">
            {
              this.isEnded ?
                i18n.t('sell_card_title_ended') :
                i18n.t('sell_card_title')
            }
          </p>
          <TimeLeft className="sell-card-time-left" />
          <p className="sell-card-text-process">{progress} {i18n.t('ico-completed')}</p>
          <Progress strokeWidth={strokeWidth} className="main-progress" percentage={this.props.progress} showText={false} />
          <p className="sell-card-text-exchange">1 EOS = {this.props.exchangeRate} {this.props.name}</p>
          <Button
            className="buy-token-btn"
            type="primary"
            onClick={this.onClick}
          >
            {
              this.isEnded ?
                i18n.t('join_telegram') :
                i18n.t('buy_token')
            }
          </Button>
        </Card>
        <Modal
          contentType={this.state.modalType}
          open={this.state.modalOpen}
          onClose={this.onModalClose}
          onConfirm={this.onModalConfirm}
          data={{coinType, PRDTQuantity, EOSQuantity, account}}
        >
        </Modal>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    progress: state.icoInfo.progress * 100,
    account: state.icoInfo.account,
    end: state.icoInfo.end,
    name: state.icoInfo.name,
    exchangeRate: state.icoInfo.exchangeRate,
    identity: state.identity,
    timeLeft: state.timeLeft,
    socialContact: state.socialContact
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);
