import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { createPortal } from 'react-dom';
import Form from '../MsgBox/Form';
import Buy from '../MsgBox/Buy';
import Success from '../MsgBox/Success';
import './modal.scss'

class Modal extends Component {
  static defaultProps = {
    contentType: 'modal-from',
    open: false,
    onClose: () => void (0),
    onConfirm: () => void (0),
  };

  state = {
    open: this.props.open
  };

  onClose = () => {
    this.setState({ open: false });
    if (this.props.onClose) this.props.onClose();
  };

  onConfirm = () => {
    if (this.props.onConfirm) this.props.onConfirm();
    // 不关闭的话 this.props.onConfirm中return
    this.onClose();
  };

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.open !== prevState.open) {
      return {
        open: nextProps.open
      }
    }
    return null
  }

  render() {
    let content;
    switch (this.props.contentType) {
      case 'modal-buy':
        content = <Buy close={this.onClose} confirm={this.props.onConfirm} data={this.props.data} />;
        break;
      case 'modal-form':
        content = <Form close={this.onClose} confirm={this.props.onConfirm} />;
        break;
      case 'modal-success':
        content = <Success close={this.onClose} confirm={this.props.onConfirm} />;
        break;
      default: content = this.props.children;
    }

    // 弹窗open时禁止底部页面滑动
    const scrollWrapper = document.getElementsByClassName('app-container')[0];
    if (scrollWrapper) {
      document.getElementsByClassName('app-container')[0].style.overflow = this.state.open ? 'hidden' : 'auto';
    }

    return createPortal((
      <div className={`modal-wrapper ${this.state.open && 'modal-wrapper-active'}`}>
        <div className={`modal-content-wrapper ${this.state.open && 'modal-content-wrapper-active'}`}>
          { content }
        </div>
      </div>
    ), document.getElementById('root'))
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Modal);
