import {
  ACTION_TEST,
  UPDATE_IDENTITY,
  CHANGE_LANGUAGE,
  UPDATE_PRDT_INFO
} from '../actions';
import {UPDATE_REMOTE_TIME, UPDATE_TIME_LEFT} from '../actions/time';
import i18n from '../language';

const initialState = {
  i18n,
  language: void (0),
  socialContact: 'https://t.me/joinchat/Esi1OkPktgcFeJ3Lmlcrqp',
  customerService: 'https://t.me/joinchat/Esi1OkPktgcFeJ3Lmlcrqp',
  icoInfo: {
    name: 'TTC',
    contract: 'prdttesticco',
    type: 'EOS Token',
    maxSupply: 0, // token总量
    symbol: 'TTC',
    startTime: 0,
    endTime: 0,
    exchangeRate: 100,
    minPayments: 1,
    progress: 0,
    occupy: 0.4, // 销售占比
    account: 'prdttesticco',
    end: false
  },
  identity: null,
  timeOffset: 0, // 服务器时间校正
  timeLeft: 0, // 当局剩余时间
  dappUrl: 'http://10.10.0.52:3100',
  // timeTotal: 30 * 24 * 3600 * 1000, // ico周期
  FAQ: [
    {
      question: () => i18n.t('qu1'),
      answer: () => i18n.t('ans1')
    },
    {
      question: () => i18n.t('qu2'),
      answer: () => i18n.t('ans2')
    },
    {
      question: () => i18n.t('qu3'),
      answer: () => i18n.t('ans3')
    }
  ]
};

export default function (state = initialState, action) {
  switch (action.type) {
    case ACTION_TEST:
      return {...state, data: action.payload.data};
    case CHANGE_LANGUAGE:
      return {...state, language: action.payload.language};
    case UPDATE_IDENTITY:
      return {...state, identity: action.payload.identity};
    case UPDATE_REMOTE_TIME:
      return {...state, timeOffset: action.payload.timeOffset};
    case UPDATE_TIME_LEFT:
      return {...state, timeLeft: action.payload.timeLeft};
    case UPDATE_PRDT_INFO:
      return {...state, icoInfo: {...state.icoInfo, ...action.payload}};
    default:
      return state;
  }
};
