import React, { Component } from 'react';
import { createPortal } from 'react-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import LanguagePicker from '../Parts/LanguagePicker/LanguagePicker';
import { login, logout } from '../../actions';
import Share from '../Share/Share';
import i18n from '../../language';
import images from '../../images';
import './header.scss';

class Header extends Component {
  state = {
    openMenu: false,
    showLogout: false
  };

  toggle = () => {
    this.setState(({ openMenu }) => ({ openMenu: !openMenu }))
  };

  toggleLogout = (showLogout) => {
    this.setState({showLogout})
  };

  logout = () => {
    this.props.logout();
    this.toggleLogout(false)
  };

  scrollTo = (selector) => {
    if (this.state.openMenu) this.toggle();
    document.querySelector(selector).scrollIntoView({behavior: 'smooth', block: 'start'})
  };

  render() {
    const { identity } = this.props;
    const { openMenu } = this.state;
    return (
      <Layout.Row className="header-wrapper">
        <Layout.Col xs="12" sm="12" md="12" lg="6" className="header-logo-box">
          <span className="header-logo">logo</span>
          <span className="text-normal-light">{i18n.t('app_name')}</span>
        </Layout.Col>
        <Layout.Col xs="0" sm="0" md="0" lg="12" className="header-menus-box">
          <span className="header-link-row" onClick={() => this.scrollTo('#anchor-introduce')}>{i18n.t('token_introduce')}</span>
          <span className="header-link-row" onClick={() => this.scrollTo('#anchor-sell')}>{i18n.t('token_sell')}</span>
          <span className="header-link-row" onClick={() => this.scrollTo('#anchor-dapp')}>{i18n.t('dapp')}</span>
          <span className="header-link-row" onClick={() => this.scrollTo('#anchor-faq')}>{i18n.t('FAQ')}</span>
          <span className="header-link-row" onClick={() => this.scrollTo('#anchor-footer')}>{i18n.t('contract_us')}</span>
          <Share
            handleClick={() => {
              if (openMenu) {
                this.toggle()
              }
            }}
            className="header-link-row"
          />
          <LanguagePicker />
        </Layout.Col>
        <Layout.Col xs="8" sm="8" md="8" lg="6" className="header-btn-drop-box">
          <div className="login-wrapper">
            {
              identity ?
                <Button size="mini" type="text" onClick={() => this.toggleLogout(true)}>
                  <span className="text-small-light">{identity.name}</span>
                  <img className="header-menu-drop-down" src={images.dropDown} alt=""/>
                </Button> :
                <Button size="mini" type="text" onClick={this.props.login}>
                  <span className="text-small-light">{i18n.t('login')}</span>
                </Button>
            }
            {
              this.state.showLogout &&
              <Button className="login-out-btn" size="mini" type="danger" onClick={this.logout}>
                <span className="text-small-light">{i18n.t('logout')}</span>
              </Button>
            }
            {
              this.state.showLogout &&
              createPortal(
                <div onClick={() => this.toggleLogout(false)} className="header-modal" />,
                document.getElementById('root')
              )
            }
          </div>
        </Layout.Col>
        <Layout.Col xs="4" sm="4" md="4" lg="0" className="header-btn-box">
          <div onClick={this.toggle} className="header-menu-btn">
             <img className="header-img-menu" src={openMenu ? images.menuClose : images.menu} alt=""/>
          </div>
        </Layout.Col>
        {
          createPortal(
            (
              <div onClick={this.toggle} className={openMenu ? 'header-modal-active' : 'header-modal-inactive'}>
                <div onClick={(e) => e.stopPropagation() } className={openMenu ? 'header-menu-content-active' : 'header-menu-content'}>
                  <p className="header-link" onClick={() => this.scrollTo('#anchor-introduce')}>{i18n.t('token_introduce')}</p>
                  <p className="header-link" onClick={() => this.scrollTo('#anchor-sell')}>{i18n.t('token_sell')}</p>
                  <p className="header-link" onClick={() => this.scrollTo('#anchor-dapp')}>{i18n.t('dapp')}</p>
                  <p className="header-link" onClick={() => this.scrollTo('#anchor-faq')}>{i18n.t('FAQ')}</p>
                  <p className="header-link" onClick={() => this.scrollTo('#anchor-footer')}>{i18n.t('contract_us')}</p>
                  <Share
                    handleClick={() => {
                      if (openMenu) {
                        this.toggle()
                      }
                    }}
                    className="header-link"
                  />
                  <LanguagePicker />
                </div>
              </div>
            ),
            document.getElementById('root')
          )
        }
      </Layout.Row>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    identity: state.identity
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({login, logout}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
