import React, { Component } from 'react';
import { connect } from 'react-redux';
import Button from 'element-react/dist/npm/es5/src/button';
import SummaryTitle from '../Parts/SummaryTitle/SummaryTitle';
import images from '../../images';
import i18n from '../../language';
import './dapp.scss';

class Dapp extends Component {
  render() {
    return (
      <div className="summary-wrapper-light">
        <div id="anchor-dapp" className="anchor" />
        <div className="content-center-wrapper">
          <SummaryTitle
            index="03"
            title={i18n.t('dapp')}
          />
          <p className="dapp-text-content">{i18n.t('vision_content')}</p>
          <div className="column-center">
            <p className="dapp-text-coming">{ i18n.t('PRDT_is_coming') }</p>
            <img className="dapp-img-logo" src={images.endless} alt="PRDT"/>
            <p className="text-medium-dark">Endless 30s</p>
            <p className="text-medium-grey align-center">{ i18n.t('PRDT_describe') }</p>
            <Button
              onClick={() => {
                window.location.href = this.props.dappUrl;
              }}
              type="primary"
            >
              { i18n.t('PRDT_btn') }
            </Button>
          </div>
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    dappUrl: state.dappUrl
  };
}

export default connect(mapStateToProps)(Dapp);
